	.stepFlow {
		$stepFlow: &;
		display: flex;
		justify-content: center;
		padding: 0;
		&__item {
			background-color: #ddd;
			font-weight: 500;
			display: flex;
			align-items: center;
			flex: 1 1 100%;
			justify-content: center;
			min-height: 80px;
			position: relative;
			padding: 10px 30px;
			&::before,
			&::after {
				content: " ";
				background-color: #ddd;
				border-right: 3px solid #fff;
				display: block;
				height: 40px;
				width: 30px;
				position: absolute;
				z-index: 1;
				right: -15px;
			}
			&::before {
				transform: skew(30deg);
				top: 0;
			}
			&::after {
				transform: skew(-30deg);
				bottom: 0;
			}
			&.is-active {
				background-color: $color-primary;
        color: #fff;
				&::before,
				&::after {
					background-color: $color-primary;
				}
			}
			&:last-child {
				&::before,
				&::after {
					content: none;
				}
			}
		}
	}
  .cangeButton {
    height: 40px;
    width: 180px;
		margin: 0 0 0 auto;
    @include font-line(14, 14);
    &::after {
      right: 5px;
    }
  }
  .content {
    padding: 50px 0;
  }
